import { useState, useEffect } from "react";
import styled from "styled-components";

const initialBoxState = [
  { id: 1, value: "" },
  { id: 2, value: "" },
  { id: 3, value: "" },
  { id: 4, value: "" },
  { id: 5, value: "" },
  { id: 6, value: "" },
  { id: 7, value: "" },
  { id: 8, value: "" },
  { id: 9, value: "" }
];

const winSequences = ["123", "456", "789", "147", "258", "369", "159", "357"];

const isGameWon = (arr) => {
  let decision = false;
  winSequences.forEach((sequence) => {
    let local = [];
    sequence.split("").forEach((numb) => {
      const target = arr.find((box) => box.id == parseInt(numb));
      if (target.value == local[0] || local.length == 0) {
        local.push(target.value);
      }
    });
    if (local.length == 3 && local[0] != "") {
      decision = true;
    }
  });
  return decision;
};

export const Game = () => {
  const [boxes, setBoxes] = useState(initialBoxState);
  const [currentPlayer, setCurrentPlayer] = useState("X");
  const [winner, setWinner] = useState("");

  useEffect(() => {
    if (isGameWon(boxes)) {
      if (currentPlayer == "X") {
        setWinner("O");
      } else {
        setWinner("X");
      }
    }
  }, [currentPlayer]);

  const clickHandler = (e) => {
    const { id } = e.target;
    const target = boxes.find((box) => box.id == id);
    if (target?.value === "") {
      setBoxes((prev) => [
        ...prev.map((box) => {
          if (box.id == id) {
            return { id, value: currentPlayer };
          } else {
            return box;
          }
        })
      ]);
      if (currentPlayer === "X") {
        setCurrentPlayer("O");
      } else if (currentPlayer === "O") {
        setCurrentPlayer("X");
      }
    }
  };
  return (
    <div>
      {winner ? (
        <button
          onClick={() => {
            setBoxes(initialBoxState);
            setCurrentPlayer("X");
            setWinner("");
          }}
        >
          Reset game
        </button>
      ) : (
        <h2>
          Current Player :{" "}
          <span style={{ color: "orange" }}>{currentPlayer}</span>
        </h2>
      )}
      {winner ? (
        <h1>{winner} won the game</h1>
      ) : (
        <Grid>
          {boxes.map((box) => {
            return (
              <Box
                selected={box.value !== ""}
                key={box.id}
                id={box.id}
                onClick={clickHandler}
              >
                {box.value}
              </Box>
            );
          })}
        </Grid>
      )}
    </div>
  );
};

const Box = styled.div`
  width: 100px;
  height: 100px;
  border: 2px solid black;
  background: ${(props) => (props.selected ? "#3f51b5" : "#dcdcdc")};
  color: #fff;
  display: flex;
  font-size: 32px;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: transform 0.3s ease;
  &:hover {
    transform: scale(1.1);
  }
`;

const Grid = styled.div`
  margin: auto;
  display: grid;
  grid-template-columns: 100px 100px 100px;
`;
